import {
  ImageList,
  ImageListItem,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import React from "react";

export interface Image {
  key: string;
  url: string;
  title: string;
}

export const ImageViewer = ({ images }: { images: Image[] }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const isTablet = useMediaQuery(theme.breakpoints.down("md"));
  return (
    <ImageList
      variant="masonry"
      cols={isMobile ? 2 : isTablet ? 3 : 4}
      gap={30}
      sx={{ padding: "15px" }}
    >
      {images.map((image) => (
        <ImageListItem key={image.key} sx={{ boxShadow: `10px 10px` }}>
          <img src={image.url} alt={image.title} loading="lazy" />
        </ImageListItem>
      ))}
    </ImageList>
  );
};
