import React, { useEffect, useState } from "react";
import axios, { AxiosResponse } from "axios";
import { Typography, Box, Button, LinearProgress } from "@mui/material";
import OpenInNewIcon from "@mui/icons-material/OpenInNew";
import { colors } from "colors";

interface MediumRSSFeed {
  items: FeedItem[];
}

interface FeedItem {
  title: string;
  pubDate: string;
  link: string;
}

export const Blogs = () => {
  const [blogs, setBlogs] = useState<MediumRSSFeed>({ items: [] });
  const [isLoading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const mediumRssFeed: Promise<AxiosResponse<MediumRSSFeed>> = axios.get(
      "https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/@rajendrasatpute/feed"
    );
    mediumRssFeed
      .then(({ data }) => {
        console.log(data);
        setBlogs(data);
      })
      .catch(() => setBlogs({ items: [] }))
      .finally(() => setLoading(false));
  }, []);

  const getBlogs = () =>
    blogs.items.length ? (
      blogs.items.map((item) => (
        <Box sx={{ my: "1rem" }}>
          <Button
            key={item.title}
            href={item.link}
            target="_blank"
            rel="noopener noreferrer"
            endIcon={<OpenInNewIcon color="secondary" fontSize="small" />}
            sx={{
              width: "fit-content",
              textTransform: "none",
            }}
          >
            <Typography
              variant="h6"
              sx={{
                fontFamily: "SpaceMono",
              }}
            >
              {item.title}
            </Typography>
          </Button>
          <Typography variant="body2" sx={{ color: colors.green, mx: "1rem" }}>
            {new Date(item.pubDate).toDateString()}
          </Typography>
        </Box>
      ))
    ) : (
      <Typography variant="body1" sx={{ my: "1rem" }}>
        There was a problem while loading the blogs...
      </Typography>
    );

  const getLoader = () => (
    <Box sx={{ py: "1rem" }}>
      <LinearProgress sx={{ my: "1rem", height: "1rem" }} />
      <LinearProgress sx={{ my: "1rem" }} />
    </Box>
  );

  return (
    <>
      <Typography variant="h5" sx={{ my: "1rem" }}>
        Medium blogs
      </Typography>
      <Box
        sx={{
          height: "1px",
          borderRadius: "1px",
          backgroundColor: colors.lightGrey,
          width: "50px",
          marginRight: "1rem",
        }}
      />
      {isLoading ? getLoader() : getBlogs()}
    </>
  );
};
