import React from "react";
import { Image, ImageViewer } from "./ImageViewer";

const images: Image[] = Array.from(Array(10).keys()).map((num) => ({
  key: `img${num}`,
  url: `images/photography/${num}.webp`,
  title: `img${num}`,
}));

export const Photography = () => <ImageViewer images={images} />;
