import { Box, Tab, Tabs, useMediaQuery, useTheme } from "@mui/material";
import { colors } from "colors";
import React, { ReactElement, useState } from "react";
import { PageTemplate } from "../template/PageTemplate";
import { SectionNavigation } from "./navigation";

export const SectionTemplate = ({
  sectionNavigation,
  pretext,
}: {
  sectionNavigation: SectionNavigation[];
  pretext?: ReactElement;
}) => {
  const [currentSelection, setCurrentSelection] = useState<number>(0);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setCurrentSelection(newValue);
  };

  return (
    <PageTemplate>
      <>
        {pretext}
        <Box
          sx={{
            display: "flex",
            flexDirection: {
              xs: "column",
              md: "row",
            },
          }}
        >
          <Box
            sx={{
              marginRight: "1.5rem",
              height: "100%",
              marginBottom: { xs: "2rem", md: "auto" },
            }}
          >
            <Tabs
              value={currentSelection}
              onChange={handleChange}
              aria-label="basic tabs example"
              textColor="secondary"
              indicatorColor="secondary"
              variant="scrollable"
              scrollButtons="auto"
              orientation={isMobile ? "horizontal" : "vertical"}
              visibleScrollbar
              sx={{
                width: { md: "170px" },
              }}
            >
              {sectionNavigation.map((section, index) => (
                <Tab
                  label={section.title}
                  sx={{
                    color:
                      index === currentSelection
                        ? colors.light
                        : colors.lightGrey,
                    my: { md: "1rem" },
                    fontSize: index === currentSelection ? "1rem" : "0.75rem",
                    alignItems: "flex-end",
                  }}
                />
              ))}
            </Tabs>
          </Box>
          <Box
            sx={{
              height: "100%",
              width: "100%",
              overflowY: "auto",
              marginLeft: { xs: 0, md: "2rem" },
            }}
          >
            {sectionNavigation[currentSelection].component}
          </Box>
        </Box>
      </>
    </PageTemplate>
  );
};
